.app{
  height: 100vh;


  .sections{
    width: 100%;
    height: calc(100vh - 100px);
    position: relative;
    top: 100px;
    scroll-behavior: smooth;

    scroll-snap-type: y mandatory;

    > *{
      width: 100vw;
      height: calc(100vh - 100px);
      scroll-snap-align: start;
    }
  }
}
